import { Component, OnInit } from '@angular/core';
import { CrudService } from '../services/crud.service';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { storage } from 'firebase';

/**  */
interface Item {
  id: string;
  name: string;
  age: number;
}
export interface location {
  lat: number, lng: number, url: string
}

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})

export class LandingComponent implements OnInit {
  focus: any;
  focus1: any;
  zoom: number = 2;
  public darkstyle = [
    {
      "stylers": [
        {
          "visibility": "off"
        }
      ]
    },
    {
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#616161"
        }
      ]
    },
    {
      "elementType": "geometry.fill",
      "stylers": [
        {
          "color": "#616161"
        },
        {
          "visibility": "on"
        }
      ]
    },

    {
      "featureType": "water",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#444343"
        }
      ]
    },


  ]
  black_points_string = ['london', 'berlin', 'nyc', 'tokyi', 'boston and chicago'];

  Ads_points = [];
  Black_points = [];
  ads: any;
  home_images: any;
  items: Observable<Item[]>;
  t_types =
    [
      {
        name: 'Majors',
        index: 6
      },
      {
        name: 'Marathons',
        index: 1
      },
      {
        name: 'Half Marathons',
        index: 2
      },
      {
        name: 'Triathlons',
        index: 3
      },
      {
        name: 'Events near your',
        index: 7
      },
      {
        name: 'Trails/Ultra',
        index: 4
      },
      {
        name: 'Runtrip Expiriences',
        index: 8
      },
      {
        name: 'Otras categoras',
        index: 5
      },
    ];
  constructor(private CrudService: CrudService, private Router: Router) {
    this.home_images = {
      Home1: {
        img: '',
        link: ''
      },
      Home2: {
        img: '',
        link: ''
      }
    };

    this.black_points_string.forEach(black_point => {
      this.CrudService.getLocation(black_point, '').subscribe(res => {
        const markerObject = { lat: res.data[0].result_object.latitude, lng: res.data[0].result_object.longitude, marker: { url: './assets/img/icon/marker_2.gif', scaledSize: { height: 40, width: 40 } } };
        this.Black_points.push(markerObject);
      });
    });
  }

  filter_data = {
    location: '',
    date: '',
    event_type: 1
  };
  event_type = '';

  ngOnInit() {
    let self = this;
    self.CrudService.getProducts().subscribe(res => {
      self.home_images = res[2];
      localStorage.setItem('home_images' , JSON.stringify(self.home_images));
    
      self.ads = [];
      Object.keys(res[0]).forEach(function (rkey) {
        self.ads.push(res[0][rkey]);
      });
      self.ads.forEach(ad => {
        self.CrudService.getLocation(ad.ciudad, ad.desc).subscribe(location => {
          const markerObject = { lat: location.data[0].result_object.latitude, lng: location.data[0].result_object.longitude, marker: { url: './assets/img/icon/marker_0.gif', scaledSize: { height: 40, width: 40 } } };
          self.Ads_points.push(markerObject);
        });
      });

    });

  }
  filter(event_type) {
    // this.filter_data.event_type = this.types.indexOf(event_type) + 1;
    console.log('event_type', event_type);
    this.filter_data.event_type = event_type;
    let str = this.filter_data.date.split('-');
    this.filter_data.date = str[1] + '/' + str[2] + '/' + str[0];
    this.Router.navigate(['/filter', { 'location': this.filter_data.location, 'event_type': this.filter_data.event_type, 'date': this.filter_data.date }]);   
  }


}